import React, { useContext, useState } from 'react';

import Modal from '../UI/Modal';
import CartItem from './CartItem';
import classes from './Cart.module.css';
import CartContext from '../../store/cart-context';
import Checkout from './Checkout';
import successfullImage from '../../assets/thumbs.png';

const Cart = (props) => {
  const cartCtx = useContext(CartContext);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [didSubmit, setDidSubmit] = useState(false);
  const [isCheckout, setIsCheckout] = useState(false);

  const totalAmount = `$${cartCtx.totalAmount.toFixed(2)}`;
  const hasItems = cartCtx.items.length > 0;

  const cartItemRemoveHandler = (id) => {
    cartCtx.removeItem(id);
  };

  const cartItemAddHandler = (item) => {
    cartCtx.addItem({ ...item, amount: 1 });
  };

  //submit data to db by http-request
  const sumbitOrderHandler = async (userData) => {
    setIsSubmitting(true);
    await fetch('https://react-http-ae77c-default-rtdb.firebaseio.com/orders.json',
      {
        method: 'POST',
        body: JSON.stringify({
          user: userData,
          orderItems: cartCtx.items
        })
      });
    setIsSubmitting(false);
    setDidSubmit(true);
    cartCtx.clearCart();
  };

  const cartItems = (
    <ul className={classes['cart-items']}>
      {cartCtx.items.map((item) => (
        <CartItem
          key={item.id}
          name={item.name}
          amount={item.amount}
          price={item.price}
          onRemove={cartItemRemoveHandler.bind(null, item.id)}
          onAdd={cartItemAddHandler.bind(null, item)}
        />
      ))}
    </ul>
  );

  const orderHandler = () => {
    setIsCheckout(true);
  }

  const modalAction = <div className={classes.actions}>
    <button className={classes['button--alt']} onClick={props.onClose}>
      Close
  </button>
    {hasItems && <button className={classes.button} onClick={orderHandler}>Order</button>}
  </div>

  //function to handle modal content
  const cartModalContent = <React.Fragment>
    {cartItems}
    < div className={classes.total} >
      <span>Total Amount</span>
      <span>{totalAmount}</span>
    </div >
    {isCheckout && <Checkout onConfirm={sumbitOrderHandler} onCancel={props.onClose} />}
    {!isCheckout && modalAction}
  </React.Fragment>

  const isSubmittingModalContent = <p>Confirming order...</p>;
  const didSubmitModalContent = <React.Fragment>
    <p className={classes.ordertext}>Order Confirmed.</p>
    <div className={classes.ordertext}>
        <img src={successfullImage} alt='Delicious food on your way!' />
      </div>
    <div className={classes.actions}>
    <button className={classes.button} onClick={props.onClose}>
      Close
  </button>   
  </div>
    </React.Fragment>

  return (
    <Modal onClose={props.onClose}>
      {!isSubmitting && !didSubmit && cartModalContent}
      {isSubmitting && isSubmittingModalContent}
      {!isSubmitting && didSubmit && didSubmitModalContent}
    </Modal>
  );
};

export default Cart;
